










import { Component, Prop, Vue } from 'vue-property-decorator';
import { State, Action, Mutation } from 'vuex-class';

import * as types from '@/store/mutation-types';

@Component({
  name: 'SplashContainer',
  components: {
  },
})
export default class SplashContainer extends Vue {
  @Prop() public title: string;
  @Prop() public centered: boolean;

  @State('splashPage') public splashPage;

  @Action(types.SET_PAGE_TITLE) public setPageTitle;
  @Mutation(types.SET_SPLASH_MODE) public setSplashMode;

  private mounted() {
    const that = this as any;
    that.$material.theming.theme = 'dark';

    this.setPageTitle(this.title);
    this.setSplashMode(true);
  }

  private beforeDestroy() {
    this.setSplashMode(false);
  }
}



