


























import { Component, Prop, Vue } from 'vue-property-decorator';
import { State, Action, Mutation } from 'vuex-class';

import Preloader from '@/components/Preloader.vue';
import MainHeader from '@/template/MainHeader.vue';
import MainNav from '@/template/MainNav.vue';
import MainFooter from '@/template/MainFooter.vue';
import PageContainer from '@/components/PageContainer.vue';

@Component({
  name: 'App',
  components: {
    Preloader,
    MainHeader,
    MainNav,
    MainFooter,
    PageContainer,
  },
})
export default class App extends Vue {
  public loading: boolean = false;
  public message: boolean = false;

  @State('splashPage') public isSplash;

  get containerClass(): any {
    return {
      splash: this.isSplash
    };
  }

  public closeMessage() {
    this.message = false;
  }

  public beforeRouteRender(to, from, next) {
    this.loading = true;
    next();
  }

  public afterRouteRender() {
    this.loading = false;
  }

  private created() {
    this.$router.beforeEach(this.beforeRouteRender);
    this.$router.afterEach(this.afterRouteRender);
  }

  private mounted() {
    window.setTimeout(() => {
      this.message = true;
    }, 2000);
  }
}



