import { State, Action, Mutation } from 'vuex-class';

import Logo from '@/components/Logo.vue';
import * as types from '@/store/mutation-types';

@Component({
  name: 'MainHeader',
  components: {
    Logo,
  },
})
export default class MainHeader extends Vue {
  public themeMenuActive: boolean = false;

  @State('pageTitle') public stateTitle;
  @State('splashPage') public isSplash;
  @State('theme') public currentTheme;

  get pageTitle() {
    const { stateTitle } = this;

    return stateTitle && stateTitle;
  }

  get isHome() {
    return this.$route.path === '/';
  }

  get mainHeaderClasses() {
    const { isSplash } = this;

    return {
      'md-transparent': isSplash,
      'md-elevation-2': !isSplash,
      'md-primary': !this.currentTheme.includes('default')
    };
  }

  get mainHeaderElevation() {
    if (this.isSplash) {
      return 0;
    }

    return 2;
  }

  @Action(types.CHANGE_THEME) public setCurrentTheme;
  @Mutation(types.SHOW_MENU) public showMenu;

  public setTheme(theme) {
    this.setCurrentTheme(theme);

    window.setTimeout(() => {
      this.themeMenuActive = false;
    }, 50);
  }

  public getPrimaryClass(theme) {
    if (theme === this.currentTheme) {
      return 'md-primary';
    }
  }
}



