





















import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import { State, Action, Mutation } from 'vuex-class';

import Logo from '@/components/Logo.vue';
import MainNavContent from './MainNavContent.vue';
import * as types from '@/store/mutation-types';

@Component({
  name: 'MainNav',
  components: {
    Logo,
    MainNavContent,
  },
})
export default class MainNav extends Vue {
  public isMenuVisible: boolean = false;

  @State('splashPage') public isSplash;
  @State('menuVisible') public menuVisible;

  @Mutation(types.HIDE_MENU) public hideMenu;

  @Watch('menuVisible')
  public onMenuVisibleChanged(val: boolean, oldVal: boolean) {
    this.isMenuVisible = val;
  }

  private created() {
    this.$router.beforeEach((to, from, next) => {
      this.hideMenu();
      next();
    });
  }
}



