/// Third Party
import 'regenerator-runtime/runtime';
import Vue from 'vue';


/// App
import App from './App.vue';

import { router, store, i18n } from './config';

document.addEventListener('DOMContentLoaded', () => {
  const app = new Vue({
    name: 'Main',
    router,
    store,
    i18n,
    render: (mount) => mount(App),
  });

  router.onReady(() => {
    app.$mount('#app');
  });
});



