/* Components */
import Vue from 'vue';

import Logo from '@/components/Logo.vue';
import Preloader from '@/components/Preloader.vue';
import MdSvgLoader from '@/components/MdSvgLoader.vue';
import PageContainer from '@/components/PageContainer.vue';
import SplashContainer from '@/components/SplashContainer.vue';

Vue.component(Logo.name, Logo);
Vue.component(Preloader.name, Preloader);
Vue.component(MdSvgLoader.name, MdSvgLoader);
Vue.component(PageContainer.name, PageContainer);
Vue.component(SplashContainer.name, SplashContainer);



